import { Component, OnInit } from '@angular/core';
//Router
import { Router, ActivatedRoute } from '@angular/router';
//RXJS
import { BehaviorSubject } from 'rxjs';
import { first } from 'rxjs/operators';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Model Device
import { Service } from '../../../../../partials/layout/tacgui/_forms/tac-service-form/service.model'
//Service//
import { ServiceService } from '../service.service'

@Component({
  selector: 'kt-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

  validation = new BehaviorSubject({})
  loadingForm = new BehaviorSubject(true)

  formParameters = {
    action: 'add',
    data: new Service,
    old_data: new Service
  }

  constructor(
    private toastr:ToastrService,
    private s_service: ServiceService,
    private router:Router,
    private route: ActivatedRoute
  ) {

  }

  ngOnInit() {
    this.formParameters.data.empty()
    this.formParameters.old_data.empty()
    this.route.paramMap.pipe(
      first()
    ).subscribe( params => {
      this.fillForm(+params.get('id'))
    });
  }

  fillForm(id:number){
    console.log(id)
    this.s_service.get(id).subscribe( data => {
      console.log(data)
      if (!data){
        this.toastr.error('Server Error!')
        this.router.navigate(['../../'], { relativeTo: this.route });
        return
      }

      Object.assign(this.formParameters.data, this.makeClone(data))
      Object.assign(this.formParameters.old_data, this.makeClone(data))

      this.loadingForm.next(false)

    })
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  save(){
    this.loadingForm.next(true)
    let data = this.makeClone(this.formParameters.data)
    let old_data = this.makeClone(this.formParameters.old_data)

    if (JSON.stringify(data) == JSON.stringify(old_data)){
      this.toastr.warning('No Changes Detected!')
      this.loadingForm.next(false)
      return;
    }

    data.cisco_rs_cmd = (data.cisco_rs_cmd[0] && data.cisco_rs_cmd[0].id) ? data.cisco_rs_cmd.map( x => x.id) : null
    data.h3c_cmd = (data.h3c_cmd[0] && data.h3c_cmd[0].id) ? data.h3c_cmd.map( x => x.id) : null
    data.junos_cmd_ac = (data.junos_cmd_ac[0] && data.junos_cmd_ac[0].id) ? data.junos_cmd_ac.map( x => x.id) : null
    data.junos_cmd_dc = (data.junos_cmd_dc[0] && data.junos_cmd_dc[0].id) ? data.junos_cmd_dc.map( x => x.id) : null
    data.junos_cmd_ao = (data.junos_cmd_ao[0] && data.junos_cmd_ao[0].id) ? data.junos_cmd_ao.map( x => x.id) : null
    data.junos_cmd_do = (data.junos_cmd_do[0] && data.junos_cmd_do[0].id) ? data.junos_cmd_do.map( x => x.id) : null

    console.log(data)

    this.s_service.save(data).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loadingForm.next(false)
        return
      }
      if (data.save) {
        this.toastr.success('Service Changed!');
        this.router.navigate(['../../'], { relativeTo: this.route });
      } else {
        this.toastr.error('Unknown server error');
      }
      this.loadingForm.next(false)
    })
  }

}
