import { Component, OnInit, Input, ViewChild} from '@angular/core';
// RxJS
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'kt-tac-service-form',
  templateUrl: './tac-service-form.component.html',
  styleUrls: ['./tac-service-form.component.scss']
})
export class TacServiceFormComponent implements OnInit {

  @Input() options
  @Input() validation
  @Input() loading


  constructor(private toastr:ToastrService) { }

  ngOnInit() {
    this.validation.subscribe(data => {
      //console.log(data)
      for (let key in data) {
        if ( key == 'ace') {
          this.toastr.error('ACE Error!');
          continue;
        }
        if (data[key]) {
          //console.log(data[key])
          for (let i = 0; i < data[key].length; i++) {
              this.toastr.error(data[key][i]);
          }
        }
      }
    })
  }

  trigger(section:string){
    // console.log(this.options.data[section])
    // console.log(section)
    this.options.data[section] = +!this.options.data[section];
  }
}
