export class Service {
  id: number;
  name: string;
  cisco_rs_autocmd: string
  cisco_rs_cmd: any
  cisco_rs_debug_message: number
  cisco_rs_def_attr: number
  cisco_rs_def_cmd: number
  cisco_rs_enable: number
  cisco_rs_idletime: number
  cisco_rs_manual: string
  cisco_rs_nexus_roles: string
  cisco_rs_privlvl: number
  cisco_rs_timeout: number
  cisco_wlc_enable: number
  cisco_wlc_manual: string
  cisco_wlc_roles: any
  created_at: string
  default_cmd: number
  fortios_admin_prof: string
  fortios_enable: number
  fortios_manual: string
  h3c_cmd: any
  h3c_def_attr: number
  h3c_def_cmd: number
  h3c_enable: number
  h3c_idletime: number
  h3c_manual: string
  h3c_privlvl: number
  h3c_timeout: number
  junos_cmd_ac: any
  junos_cmd_ao: any
  junos_cmd_dc: any
  junos_cmd_do: any
  junos_enable: number
  junos_manual: string
  junos_username: string
  manual: string
  manual_conf_only: number
  paloalto_admin_domain: string
  paloalto_admin_role: string
  paloalto_enable: number
  paloalto_manual: string
  paloalto_panorama_admin_domain: string
  paloalto_panorama_admin_role: string
  paloalto_user_group: string
  // priv_lvl: number
  silverpeak_enable: number
  silverpeak_manual: string
  silverpeak_role: string
  updated_at: string

    empty(): void {
      //General Config
      this.id=undefined;
      this.name='';
      this.manual=''
      this.manual_conf_only=0
      //Service Start
      //Activated//
      this.cisco_rs_enable = 0
      this.cisco_wlc_enable = 0
      this.fortios_enable = 0
      this.h3c_enable = 0
      this.junos_enable = 0
      this.paloalto_enable = 0
      this.silverpeak_enable = 0
      //Manual//
      this.cisco_rs_manual = ''
      this.cisco_wlc_manual = ''
      this.fortios_manual = ''
      this.h3c_manual = ''
      this.junos_manual = ''
      this.paloalto_manual = ''
      this.silverpeak_manual = ''
      //WLC
      this.cisco_wlc_roles = []
      //Cisco general
      this.cisco_rs_def_attr = 1
      this.cisco_rs_def_cmd = 1
      this.cisco_rs_privlvl = 15
      this.cisco_rs_cmd = []
      this.cisco_rs_autocmd = ''
      this.cisco_rs_nexus_roles = ''
      //Juniper
      this.junos_username = ''
      this.junos_cmd_ac = []
      this.junos_cmd_ao = []
      this.junos_cmd_dc = []
      this.junos_cmd_do = []
      //H3C
      this.h3c_cmd = []
      this.h3c_privlvl = 15
      this.h3c_def_attr = 1
      this.h3c_def_cmd = 1
      //FortiOS
      this.fortios_admin_prof = ''
      //Silver Peak
      this.silverpeak_role = 'admin'
      //paloalto
      this.paloalto_admin_domain = ''
      this.paloalto_admin_role = ''
      this.paloalto_panorama_admin_domain = ''
      this.paloalto_panorama_admin_role = ''
      this.paloalto_user_group = ''

    }
}
